import Vue from 'vue'
import VueRouter from 'vue-router'

// 解决vue-router 3.x版本重复点击菜单报错问题
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

Vue.use(VueRouter)

const routes = [
  { 
    path: '/', 
    redirect: '/login' 
  },
  { 
    path: '/login', 
    name: 'Login', 
    meta: { name: '登录' }, 
    component: () => import('../views/Login.vue')
  },
  {
    path: '/admin',
    name: 'Admin',
    component: () => import('../views/admin/User.vue'),
    redirect: '/home',
    children: [
      { 
        path: 'home', 
        name: 'Home', 
        meta: { name: '系统首页' }, 
        component: () => import('../views/admin/Home.vue')
      },
      { 
        path: 'user', 
        name: 'User', 
        meta: { name: '用户信息' }, 
        component: () => import('../views/admin/User.vue')
      },
      { 
        path: '403', 
        name: 'Auth', 
        meta: { name: '无权限' }, 
        component: () => import('../views/admin/Auth.vue')
      },
      { 
        path: 'person', 
        name: 'Person', 
        meta: { name: '个人信息' }, 
        component: () => import('../views/admin/Person.vue')
      },
      { 
        path: 'password', 
        name: 'Password',  // 统一使用首字母大写命名
        meta: { name: '修改密码' }, 
        component: () => import('../views/admin/Password.vue')
      },
    ]
  },
  {
    path: '/security',
    name: 'Security',
    component: () => import('../views/security/Home.vue'),  // 添加.vue扩展名
    redirect: '/home',
    children: [
      { 
        path: 'home', 
        name: 'SecurityHome',  // 避免name重复
        meta: { name: '系统首页' }, 
        component: () => import('../views/security/Home.vue')  // 添加.vue扩展名
      }
    ]
  },
  { 
    path: '/register', 
    name: 'Register', 
    meta: { name: '注册' }, 
    component: () => import('../views/Register.vue')
  },
  { 
    path: '/forgot', 
    name: 'ForgotPassword',  // 修正命名一致性
    meta: { name: '忘记密码' },
    component: () => import('../views/ForgotPassword.vue')
  },
  { 
    path: '*', 
    name: 'NotFound',  // 更规范的命名
    meta: { name: '无法访问' }, 
    component: () => import('../views/404.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

// 增强型路由守卫
router.beforeEach((to, from, next) => {
  const adminPaths = ['/admin/user']  // 使用完整路径
  const user = JSON.parse(localStorage.getItem('honey-user') || '{}')

  // 权限验证逻辑
  if (user.role !== '管理员' && adminPaths.includes(to.path)) {
    next('/admin/403')
  } else {
    next()
  }
})

export default router